/* Container for Skills */
.skills-container {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
   
    margin:30px;
    margin-left: 80px;
  }
  
  /* Individual Skill Card */
  .skill-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f3f4f6;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease;
    width: 200px;
  }
  .add-skill-btn{
    margin-left:160px;
  }
  
  /* Hover effect for cards */
  .skill-card:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  }
  
  /* Skill Title */
  .skill-title {
    font-size: 16px;
    font-weight: 500;
    color: #333;
    margin: 0;
    flex: 1;
  }
  
  /* Delete Button */
  .delete-btn {
    background-color: #ff4d4d;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .delete-btn:hover {
    background-color: #e60000;
  }
  
  .delete-btn:active {
    background-color: #cc0000;
  }
  