.techstack{
    padding-left:100px;
    margin-top: 60px;
}
.techstack hr{
    color:#138781;
    width:400px;
    margin:10px auto;
}
.techstack h2{
    font-weight: bold;
    color:#138781;
}
.techstack .card{
    cursor:pointer;
    width:200px;
}
.techstack .card:hover{
    background-color: #138781;
    color:white;
    border:none;
}
.techstack .tech-icon{
    font-size: 2 rem;
    margin-right: 10px;
    transition:1s;
}
@media(max-width:600px){
.techstack{
    margin:0;
    padding:0;
    width:100%;
}
}