/* General Styling */
.about {
   padding: 60px 20px;
   background-color: #ffffff;
   color: #333;
   font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
 }
 
 /* Flexbox Layout Styling */
 .row {
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   align-items: center;
 }
 .about-content{
   margin-left: 100px;
   
 }
 
 .col-md-6 {
   flex: 1;
   min-width: 280px;
   padding: 20px;
   box-sizing: border-box;
  
 }
 
 /* Placeholder Image Styling */
 
 
 
 /* Content Styling */
 .about-content h1 {
   position: relative;
   left:500px;
   font-size: 2.8rem;
   margin-bottom: 15px;
   color: #0073e6;
   text-transform: capitalize;
   border-bottom: 4px solid #0073e6;
   display: inline-block;
 }
 
 .about-content p {
   font-size: 1.15rem;
   line-height: 1.8;
   color: #555;
   text-align: justify;
   margin-top: 20px;
 }
 
 /* Responsive Design */
 @media screen and (max-width: 768px) {
   .row {
     flex-direction: column;
   }
 
   .col-md-6 {
     text-align: center;
   }
 
   .about-content h1 {
     font-size: 2rem;
   }
 
   .about-content p {
     font-size: 1rem;
   }
 
   .about-img {
     margin-bottom: 20px;
   }
 }
 