.home-container{

color:white;
height:300px;

}
.home-content h1{
   padding:10px;
   padding-top: 50px; 
    margin-left: 100px; 
}
.home-content h2{
    padding:10px;
  color:yellow;
    margin-left: 100px;
 }
.home-buttons{
    margin-left: 100px;

    
}
.home-buttons .btn-hire{
    color:black;
    width:150px;
    background-color: aqua;
    margin-left: 20px;
    margin-right: 20px;
}
.home-buttons .btn-cv{
    color:white;
    width:150px;
    background-color:orange;
}
.admin-btn{
    position:relative;
    left:120px;
    top:10px;
    text-decoration: none;
    color:rgb(3, 8, 8);
    border: 1px solid rgb(35, 5, 5);
    border-radius: .5rem;
    padding:4px;
    background-color: aqua;
    
}
@media (max-width:600px){
    .home-container{
        padding:0;
        width:100%;
        margin:0;
        margin-top:50px;
        margin-right: 0%;
    }
    .home-container h2{
        font-size: 24px;
    }
    .btn-hire{
        position: relative;
        right:19px;
        bottom:5px;
    }
    
    .sidebar-section{
        width:100%;
    }
    .home-container{
        margin-right: 0%;
        padding-right: 0%;
    }
}
@media screen {
    .home-buttons{
        display:flex;
        flex-direction: column;
    }
    .container h2{
        margin-top: -17px;
    }
    .btn-hire{
        position: relative;
        right:19px;
        bottom:5px;
    }
}

