.sidebar-section{
    display:flex;
    background-color: black;

}
.sidebar{
    height:100vh;
    width:90px;
    background-color: #1e2e3c;
    position:fixed;

}
.toggle-sidebar{
    width:200px;
}
.sidebar-toggle-icons{
    display:flex;
    align-items: right;
    justify-content: right;
    cursor:pointer;
}
.sidebar-toggle-icons p svg{
    background-color: #f29f67;
    color:white;
    margin-top: 25px;
}
/* //mobile view */
@media(max-width:600px){

    .sidebar{
        display:none;
    }
}