.navbar-profile-pic{
    height:150px;
    width:150px;
    display:flex;
    border-radius: 90px;
    border:1px solid white;
    vertical-align: middle;
    margin: 0 auto;
    margin-top: 10px;
}
.nav-link{
    color:white;
    margin-top: 10px;
    cursor:pointer
}

.nav-item{
    margin-top: 10px;
    margin-left: 30px;
}
.nav-link svg{
    margin-right: 10px;
}
.nav-link:hover{
    color:#f29f67;
}
.nav-link .active{
    color:#f29f67;
    border-left: 5px solid #f29f67;
    padding-left: 10px;
    text-decoration: none;
}