/* General Card Styles */
.project-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    margin: 10px 0;
    margin-top:30px;
    
    text-decoration: none;
    width:90%;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease;
  }
  
  /* Hover effect for the card */
  .project-card:hover {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
  }
  
  .add-project-btn{
    margin-left: 100px;
  }

  .info-msg {
    color: #ff9900;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
    position:relative;
    left:400px;
    
  }
  /* Project Title Styles */
  .project-title {
    margin: 0;
    font-size: 18px;
    font-weight: 600;
    color: #333;
  }
  
  /* Delete Button Styles */
  .delete-btn {
    background-color: #ff4d4d;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 8px 12px;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .delete-btn:hover {
    background-color: #e60000;
  }
  
  .delete-btn:active {
    background-color: #cc0000;
  }
  