.contact {
    margin-left: 130px;
    margin-bottom: 60px;
    padding-top: 50px;
  }
  
  .card0 {
    box-shadow: 0px 4px 8px 0px #757575;
    border-radius: 0px;
    width:1220px;
  }
  
  .card2 {
    margin: 0px 40px;
    position: relative;
    
  }
  
  .card1 .image {
    padding: 30px;
    margin-top: 30px;
    height: 100%;
    width: 100%;

  }
  .border-line {
    border-right: 1px solid #e0e0e0;
  }
  .line {
    height: 1px;
    width: 45%;
    background-color: #e0e0e0;
    margin-top: 10px;
  }
  
  .or {
    display: flex;
    width: 10%;
    font-weight: bold;
  }
  
  ::placeholder {
    color: #bdbdbd;
    opacity: 1;
    font-weight: 300;
  }
  
  input,
  textarea {
    padding: 10px 12px;
    border: 1px solid lightgray;
    border-radius: 2px;
    margin-top: 5px;
    margin-top: 2px;
    width: 100%;
    color: #138781;
    font-size: 1rem;
    letter-spacing: 1px;
  }
  
  input:focus,
  textarea:focus {
    box-shadow: none !important;
    outline: 1px solid #138781 !important;
    outline-width: 0px;
  }
  
  button:focus {
    box-shadow: none;
    outline-width: 0px;
  }
  
  .button {
    background-color: #1e1e2c;
    color: white;
    border-radius: 2px;
    margin-top: 15px;
    padding: 10px;
  }
  
  .button:hover {
    background-color: #000;
    color: white;
  }

  .card2 h6 {
    font-weight: bold;
    font-size: 1.25rem;
  }
  
  .btn-contact {
   width:100%;
    font-size: 1rem;
    padding: 10px 20px;
    border-radius: 8px;
    text-align: left;
    display: flex;
    align-items: center;
    text-decoration: none;
    transition: background-color 0.3s ease;
  }
  
  .btn:hover {
    opacity: 0.9;
  }
  
  
  /* ======================== */
  @media (max-width: 600px) {
    .contact {
      margin: 0;
      padding: 0;
      margin-top: 50px;
    }
    .contact .card2 {
      margin: 0;
    }
  
    .contact .card2 h6 {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .card1 .image{
      width:350px;
      height:300px;
      margin:0;

    }
    
  }