.add-skill-page {
  
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 200px;
  }
  
  .add-skill-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 300px;
  }
  
  .skill-input {
    padding: 8px;
    border: 1px solid black;
    border-radius: 4px;
    font-size: 16px;
  }
  
  .add-skill-btn {
    position:relative;
    right:50px;

    background-color: #4caf50;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .add-skill-btn:hover {
    background-color: #45a049;
  }
  
  .message {
    margin-top: 10px;
    font-size: 14px;
    color: green;
  }
  